* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-list-item {
  display: flex;
  align-items: center;
}

.country-list-item:not(:last-child) {
  margin-bottom: 10px;
}

.country-title {
  margin: 0px 0px 0px 10px;
  font-size: 16px;
  font-weight: 400;
}

.country-info-title {
  margin: 0;
  font-size: 30px;
}